<template>
  <div class="hello">
    <h1>Schedule for {{ date }}</h1>
  </div>
</template>

<script>
  export default {
    name: 'Welcome',
    props: [
      'date'
    ],
    data () {
      return {
        msg: ''
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }
</style>



